$purple: #4460f7;
$purple_dark: #2140e8;
$orange: #f9a52b;
$light_grey_1: #f0f1f5;
$light_grey_2: #e0e2ea;
$light_grey_3: #b9bdcf;
$light_grey_4: #f2f2f2;
$dark_grey_1: #9194a5;
$dark_grey_2: #1a1b1d;
$white: #ffff;
$error: #610a0a;

$mobile: 36rem;

.logo {
  font-weight: 600;
  font-size: 1.5rem;
  line-height: 2.5rem;
  color: $dark_grey_2;
}

.h1-30-bold {
  font-size: 1.875rem;
  line-height: 2.5rem;
  font-weight: 600;
  color: $dark_grey_2;
}

.h3-18-semi-bold {
  font-size: 1.125rem;
  line-height: 1rem;
  color: $dark_grey_2;
  font-weight: 600;
}

.font-14-semi-bold {
  font-size: 0.875rem;
  line-height: 1rem;
  font-weight: 600;
}

.font-12-error {
  font-size: 0.75rem;
  font-weight: 600;
  line-height: 0.875rem;
  color: $error;
}
